.App {
  text-align: center;
}

body {
  background-color: black;
}

h1 {
  color: white;
}

.container {
  width: 80vw;
  max-width: 600px;
  background-color: #E0E0E0;
  border-radius: 50px;
  font-size: 1.5em;
  font-weight: 600;
}

#start_stop {
  margin-right: 2vw;
}

#time-left {
  font-size: 2em;
  margin-bottom: -2vh;
}

#timer-label {
  margin-bottom: -2vh;
}

#name {
  color: white;
  font-family: fantasy;
  margin-top: 5px;
}

.timer-display {
  font-size: 1.25em;
  height: 10vh;
  margin-top: -2vh;
}

.section {
  width: 40%;
}

.row {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10vh;
  margin-top: 5vh;
}

#timer-section {
  margin-bottom: 5vh;
}

